.interval-slide-down {
    visibility: visible;
}

.interval-slide-down-enter {
    opacity: 0.1;
    -webkit-transform: translateY(-25%);
        -ms-transform: translateY(-25%);
            transform: translateY(-25%);
    visibility: visible;
}

.interval-slide-down-enter.interval-slide-down-enter-active {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: opacity 300ms cubic-bezier(0.5,0.2,0.1,0.1), -webkit-transform 300ms cubic-bezier(0.1,0.1,0.4,0.7);
    transition: opacity 300ms cubic-bezier(0.5,0.2,0.1,0.1), -webkit-transform 300ms cubic-bezier(0.1,0.1,0.4,0.7);
    -o-transition: opacity 300ms cubic-bezier(0.5,0.2,0.1,0.1), transform 300ms cubic-bezier(0.1,0.1,0.4,0.7);
    transition: opacity 300ms cubic-bezier(0.5,0.2,0.1,0.1), transform 300ms cubic-bezier(0.1,0.1,0.4,0.7);
    transition: opacity 300ms cubic-bezier(0.5,0.2,0.1,0.1), transform 300ms cubic-bezier(0.1,0.1,0.4,0.7), -webkit-transform 300ms cubic-bezier(0.1,0.1,0.4,0.7);
}



